import React from 'react';
import './App.css';
import ConfigPanel from './components/ConfigPanel';
import Mobile from './components/Mobile';
import MessagePanel from './components/MessagePanel';
import ServicePet from './service/ServicePet';
import { APP_STATES } from './util/AppStates';


class App extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      apiUrl: '',
      appState: APP_STATES.UNSET,
      items: [],
      statusmsg: '',
      typeLoad: '',
      baseUrl: ''
    }

    this.loadAllPets = this.loadAllPets.bind(this);
    this.loadPetDetail = this.loadPetDetail.bind(this);
    this.reloadAllPets = this.reloadAllPets.bind(this);
  }


  loadAllPets = (url) => {

    this.setState((state) => ({
      apiUrl: url,
      baseUrl: url,
      statusmsg: "Loading from\n" + url,
      typeLoad: 'findAll'
    }))

    const petService = new ServicePet();
    const serviceReturn = petService.getData(url, 'findAll' );

    serviceReturn
      .then(this.handleResult)
      .catch(this.handleError)
  }

  loadPetDetail = (id) => {
    console.log("Dentro do LoadDetail", id);
    let url = this.state.apiUrl;
    // URL can come with a final /
    url.endsWith('/') ? url =  url + id : url = url + '/' + id;
    
    this.setState((state) => ({
      apiUrl: url,
      statusmsg: "Loading from\n" + url,
      typeLoad: 'findDetail'
    }))

    const petService = new ServicePet();
    const serviceReturn = petService.getData(url, 'findDetail' );

    serviceReturn
      .then(this.handleResult)
      .catch(this.handleError)
  }

  reloadAllPets = () => {
    const url = this.state.baseUrl;
    this.loadAllPets(url);
  }

  handleResult = (res) => {
    console.log("APP ===>", res);
    const url = this.state.apiUrl;
    

    if (res.appState === APP_STATES.URL_INVALID) {
      console.log("URL INVALID => ", res.message);
      this.setState({
        appState: APP_STATES.ERROR,
        statusmsg: `\"${url}\" is not a valid url`
      });
    }

    if (res.appState === APP_STATES.URL_FORBIDDEN) {
      console.log("URL FORBIDDEN (403)", res.message);
      this.setState({
        appState: APP_STATES.ERROR,
        statusmsg: `This app is not allowed to communicate with this url \n\nGETS \n${url}\nStatus: 502 \nBody: {"message":"Missing Authentication Token"}`
      });
    }

    if (res.appState === APP_STATES.INTERNAL_SERVER_ERROR) {
      console.log("INTERNAL SERVER ERROR  (502)", res.message);
      this.setState({
        appState: APP_STATES.ERROR,
        statusmsg: `The App received a Internal Server Error from Server, \nverify your backend.\n\nGET \n${url}\nStatus: 500 \nBody: {"message": "Internal server error"}`
      });
    }


    if (res.appState === APP_STATES.FIND_ALL) {
      this.setState({
        appState: APP_STATES.FIND_ALL,
        statusmsg: `The App received data from the API. \n\nGET \n${url}\nStatus: 200 \nBody: ${JSON.stringify(res.json, null, 2)}`,
        items: res.json
      });
    }

    if (res.appState === APP_STATES.FIND_DETAIL) {
      this.setState({
        appState: APP_STATES.FIND_DETAIL,
        statusmsg: `The App received data from the API. \n\nGET \n${url}\nStatus: 200 \nBody: ${JSON.stringify(res.json, null, 2)}`,
        item: res.json
      });
    }

    return res;
  }

  handleError = (err) => {
    const url = this.state.apiUrl;
    console.log("ERRO => ", err);
    

        if (err.appState === APP_STATES.MOCK) {
          console.log("ACTION MOCK (200) No JSON");
          this.setState({
            appState: APP_STATES.MOCK,
            statusmsg: `The App received a response from the API, but the data \nis empty.\n\nGET \n${url}\nStatus: 200 \nBody: Empty`
          });
        }

        if (err.appState === APP_STATES.URL_NO_CORS) {
          console.log("NO CORS FOR THIS CLIENT " + err.message);
          this.setState({
            appState: APP_STATES.ERROR,
            statusmsg: `This app is not allowed to communicate with this url \n\nGET \n${url}\nStatus: 502 \nBody: {"message":"Missing Authentication Token"}`
          });
        }
  }

  componentDidMount() {
    this.setState({
      statusmsg: "Loading ....................OK \n\nWaiting for the configuration."
    })
  }

  render() {
    return (
      <div className="app-container">
        <div className="config">
          <ConfigPanel addNew={this.loadAllPets} />
        </div>
        <div className="message">
          <MessagePanel appState={this.state.appState} msg={this.state.statusmsg} />
        </div>

        <div className="mobile">
          <div className="mobile__screen">
            <div className="mobile__scroll-cont">
              <div className="mobile_content">
                <Mobile appState={this.state.appState} items={this.state.items} item={this.state.item} loadPetDetail={this.loadPetDetail} reloadAllPets={this.reloadAllPets}/>
              </div>
            </div>
          </div>
        </div>

      </div>
    )
  }

}

export default App;