import React from 'react';

class PetItem extends React.Component {
    
    render() {
        return (
            <div className="mobile-container">

               {this.props.items.map(item => (
                   
                <div key={item.id} className="mobile-item">
                    <h2>{item.name} </h2>
                    <div className="mobile-item-detail">
                       <div className="mobile-item-id">
                           <strong>ID</strong> {item.id}
                       </div>
                       <div className="mobile-item-type">
                           <strong>Type</strong>  {item.type} 
                       </div>
                       <button className="mobile-item-button" onClick={this.props.loadPetDetail.bind(this, item.id)}>Details</button>
                    </div>
                </div>

                ))}
                
            </div>
        )
    }
}

export default PetItem;