import React from 'react';
import PetItem from './mobile_panels/PetItem';
import PanelFullMsg from './mobile_panels/PanelFullMsg';
import { APP_STATES } from '../util/AppStates';
import PetItemDetail from './mobile_panels/PetItemDetail';


class Mobile extends React.Component {
    constructor(props){
        super(props)

        this.state = { 
            id: ''
        }

        this.details = this.details.bind(this)
    }

    details(id){
        this.props.details(id)
    }

    render() {
        switch (this.props.appState) {
            case APP_STATES.UNSET:
                return (
                    <div>
                       <PanelFullMsg header="Pet App" msg="Please, configure the app backend API to fetch the data."/>
                    </div>
                )
            case APP_STATES.LOADING:
                return (
                    <div>
                        <PanelFullMsg header="Welcome to the Pet App" msg="Loading..."/>
                    </div>
                )
            case APP_STATES.FIND_ALL:
                return (
                    <div>
                        <PetItem items={this.props.items} loadPetDetail={this.props.loadPetDetail} />
                    </div>
                )
            case APP_STATES.FIND_DETAIL:
                    return (
                        <div>
                            <PetItemDetail item={this.props.item} reloadAllPets={this.props.reloadAllPets} />
                        </div>
                    )
            case 'ERROR':
                return (
                    <div>
                        <PanelFullMsg header="Attention" msg="The Pet App couldn't fetch the data"/>
                    </div>
                )
            default:
                return (
                    <div>
                         <PanelFullMsg header="Attention" msg="The Pet App couldn't fetch the data"/>
                    </div>
                )
        }


    }
}

export default Mobile;