import React from 'react';

class PetItemDetail extends React.Component {
    
    render() {

        console.log("Props ===> ", this.props.items);
        return (
            <div className="mobile-container">

                
             <div key={this.props.item.id} className="mobile-item">
                 <h2>{this.props.item.name} </h2>
                 <div className="mobile-item-detail">
                    <div className="mobile-item-id">
                        <strong>ID</strong> {this.props.item.id}
                    </div>
                    <div className="mobile-item-type">
                        <strong>Type</strong>  {this.props.item.type} 
                    </div>
                    <div className="mobile-item-fur">
                        <strong>Fur Color</strong>  {this.props.item.fur_color} 
                    </div>
                    <button className="mobile-item-button" onClick={this.props.reloadAllPets}>Back</button>
                 </div>
             </div>


             
         </div>
        )
    }
}

export default PetItemDetail;