import React from 'react';

class ConfigPanel extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            newUrl: ''
        }

        this.updateURL = this.updateURL.bind(this);
        this.handleNewURL = this.handleNewURL.bind(this);
    }

    updateURL(e) {
        this.setState({
            newUrl: e.target.value
        })
    }

    handleNewURL() {
        this.props.addNew(this.state.newUrl);
    }


    render() {
        return (
            <div className="config-item">
                <h2>Configure your App</h2>

                <h3>Amazon API Gateway</h3>

 
                <div className='config-item-input'>
                    <input
                        type="text"
                        value={this.state.updateURL}
                        onChange={this.updateURL}
                        placeholder="https://invokeurl/prod/pets"
                    />
                </div>
                

                <h6>Format: https://restapi_id.execute-api.region.amazonaws.com/stage_name/pets</h6>
                <br/>
                <button onClick={this.handleNewURL}>Load Backend Data</button>
            </div>
        )
    }
}

export default ConfigPanel;