import { APP_STATES } from '../util/AppStates';
import ServiceReturn from './ServiceReturn';

class ServicePet {

    async getData(url, type) {
        console.log("url ", url, " type", type);
        
        if(!this.isURLValid(url)){
            return new ServiceReturn(APP_STATES.URL_INVALID, 'URL Invalid '+url, null);
        }

        const response = await fetch(url)
            .catch ( err => { 
                throw new ServiceReturn(APP_STATES.URL_NO_CORS, 'URL NO CORS FOR THIS CLIENT ' + url, null);
            });

        
        if(response.status === 403){
            return new ServiceReturn(APP_STATES.URL_FORBIDDEN, 'URL FORBIDDEN (403) ' + url, null);
        }

        if(response.status === 502 || response.status === 500){
            return new ServiceReturn(APP_STATES.INTERNAL_SERVER_ERROR, 'INTERNAL SERVER ERROR  (502) or (500)' + url, null);
        }

        if ((response.status === 200 && response.type === 'cors')) {
            const data = await response.json()
            .catch(err => {
                throw new ServiceReturn(APP_STATES.MOCK, 'MOCK Detect', null);
            })

            
            if(type === 'findAll'){
                return new ServiceReturn(APP_STATES.FIND_ALL, 'FIND ALL PETS', data);
            }else if(type === 'findDetail'){
                return new ServiceReturn(APP_STATES.FIND_DETAIL, 'FIND DETAIL', data);
            }
        }
        
    }

    isURLValid(url) {
        return /^(?:(?:(?:https?|ftp):)?\/\/)(?:\S+(?::\S*)?@)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:[/?#]\S*)?$/i.test(url);
    }
}



export default ServicePet;